export default class CssUnitE {
    constructor (assetItem) {
        this.webglAsset = assetItem.parentAsset;
        this.assetItem = assetItem;
        this.cssObject3D = null;
        this.ytPlayer = null;

        this.onReady = function () {};
    }

    //   , ,  AssetCssRenderer.__add()     .
    //   3D CSS  .
    update() {
        const me = this;

        if (me.cssObject3D) {
            const object3D = me.assetItem.object3D;

            // object3D.parent        . (      .)
            if (me.assetItem.isVisible && object3D.parent) {
                const cssObject3D = me.cssObject3D.children[0];

                cssObject3D.scale.copy(object3D.scale);
                cssObject3D.position.copy(object3D.position);
                cssObject3D.rotation.copy(object3D.rotation);

                me.showCssObject3D();

            } else {
                me.hideCssObject3D();
            }
        }
    }

    showObject3D(callEvent = true) {
        const me = this;
        me.assetItem.show(callEvent);
    }

    hideObject3D(callEvent = true) {
        const me = this;
        me.assetItem.hide(callEvent);
    }

    showCssObject3D() {
        const me = this;
        me.cssObject3D.children[0].element.style.display = '';
    }

    hideCssObject3D() {
        const me = this;
        me.cssObject3D.children[0].element.style.display = 'none';
    }
}