import Property from '../nemo-ar/property';

export default class Options {
    constructor (obj = {}) {
        /**
         *      .
         */
        this.baseUrl = obj.baseUrl || '.';

        /**
         *  .
         */
        this.rootElement = obj.rootElement || document.body;

        /**
         * .
         */
        this.width = parseFloat(obj.width) || Property.DEFAULT_WIDTH;

        /**
         * .
         */
        this.height = parseFloat(obj.height) || Property.DEFAULT_HEIGHT;

        /**
         *    .
         */
        this.hideBackground = !!obj.hideBackground;

        /**
         *    .
         */
        this.hideGrid = !!obj.hideGrid;

        /**
         *   .
         */
        this.disableAttach = !!obj.disableAttach;

        /**
         *   .
         */
        this.disableViewController = !!obj.disableViewController;

        /**
         *  .
         */
        this.cameraPosition = (typeof obj.cameraPosition == 'object') ? obj.cameraPosition : Property.CAMERA_ZERO_POSITION;

        /**
         *  .
         */
        this.cameraLookAt = (typeof obj.cameraLookAt == 'object') ? obj.cameraLookAt : Property.CAMERA_ZERO_LOOK_AT;

        /**
         *   .
         */
        this.onSelect = (typeof obj.onSelect === 'function') ? obj.onSelect : function () {};

        /**
         *   .
         */
        this.onDeselect = (typeof obj.onDeselect === 'function') ? obj.onDeselect : function () {};

        /**
         *    .
         */
        this.onMove = (typeof obj.onMove === 'function') ? obj.onMove : function () {};
    }
}